.box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 150px;
  background-color: rgba(57, 58, 74, 0.9);
  border-radius: 10px;
  padding: 10px;
  text-align: center;
}

.title {
  font-family: Bebas;
  font-size: 1.5rem;
}

.desc {
  font-family: Montserrat;
  text-align: center;
}

.cta button {
  padding: 8px 16px;
  background-color: #8fb4e3;
  ;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 4px;
  text-transform: uppercase;
  font-family: Montserrat;
  border-radius: 17px;
}